

html {
	font-family: sans-serif;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
  height: 100%;
}
body {
	margin: 0;
  height: 100%;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
	display: block;
}
a {
	background: transparent;
}
a:active, a:hover {
	outline: 0;
}
b, strong {
	font-weight: bold;
}
h1 {
	font-size: 2em;
	margin: 0.67em 0;
}
img {
	border: 0;
}
svg:not(:root) {
	overflow: hidden;
}
hr {
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
	height: 0;
}
button, input, optgroup, select, textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}
button {
	overflow: visible;
}
button, select {
	text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}
button[disabled], html input[disabled] {
	cursor: default;
}
button::-moz-focus-inner, input::-moz-focus-inner {
	border: 0;
	padding: 0;
}
input {
	line-height: normal;
}
input[type="checkbox"], input[type="radio"] {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}
input[type="search"] {
	-webkit-appearance: textfield;
	-webkit-box-sizing: content-box;
	box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}
fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

html {
	font-size: 62.5%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
*, *::before, *::after {
	-webkit-box-sizing: inherit;
	        box-sizing: inherit;
}
body {
	font-weight: normal;
	line-height: 1;
	text-rendering: optimizeLegibility;
	word-wrap: break-word;
	-webkit-overflow-scrolling: touch;
	-webkit-text-size-adjust: none;
}
body, input, button {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}

img, video {
	max-width: 100%;
	height: auto;
}

/**
 * Typography resets
 * -
 */

div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, p, blockquote, th, td {
	margin: 0;
	padding: 0;
}
h1, h2, h3, h4, h5, h6 {
	-webkit-font-variant-ligatures: common-ligatures;
	font-variant-ligatures: common-ligatures;
	text-rendering: optimizeLegibility;
}
em, i {
	font-style: italic;
	line-height: inherit;
}
strong, b {
	font-weight: bold;
	line-height: inherit;
}
small {
	font-size: 60%;
	line-height: inherit;
}
ol, ul {
	list-style: none;
}
li {
	display: block;
}
a {
	text-decoration: none;
	line-height: inherit;
}
a img {
	border: none;
}

fieldset {
	margin: 0;
	padding: 0;
}
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	-ms-appearance: none;
	-o-appearance: none;
	appearance: none;
}
.row {
	width: 96%;
	max-width: 1140px;
	margin: 0 auto;
}
.row:before, .row:after {
	content: "";
	display: table;
}
.row:after {
	clear: both;
}
.row .row {
	width: auto;
	max-width: none;
	margin-left: -20px;
	margin-right: -20px;
}
[class*="col-"], .bgrid {
	float: left;
}
[class*="col-"] + [class*="col-"]:last-child {
	float: right;
}
[class*="col-"] {
	padding: 0 20px;
}
.col-one {
	width: 8.33333%;
}
.col-two, .col-1-6 {
	width: 16.66667%;
}
.col-three, .col-1-4 {
	width: 25%;
}
.col-four, .col-1-3 {
	width: 33.33333%;
}
.col-five {
	width: 41.66667%;
}
.col-six, .col-1-2 {
	width: 50%;
}
.col-seven {
	width: 58.33333%;
}
.col-eight, .col-2-3 {
	width: 66.66667%;
}
.col-nine, .col-3-4 {
	width: 75%;
}
.col-ten, .col-5-6 {
	width: 83.33333%;
}
.col-eleven {
	width: 91.66667%;
}
.col-twelve, .col-full {
	width: 100%;
}

@media screen and (max-width:1024px) {
	.row .row {
		margin-left: -18px;
		margin-right: -18px;
	}
	[class*="col-"] {
		padding: 0 18px;
	}
}


@media screen and (max-width:768px) {
	.row {
		width: auto;
		padding-left: 30px;
		padding-right: 30px;
	}
	.row .row {
		padding-left: 0;
		padding-right: 0;
		margin-left: -15px;
		margin-right: -15px;
	}
	[class*="col-"] {
		padding: 0 15px;
	}
	.tab-1-4 {
		width: 25%;
	}
	.tab-1-3 {
		width: 33.33333%;
	}
	.tab-1-2 {
		width: 50%;
	}
	.tab-2-3 {
		width: 66.66667%;
	}
	.tab-3-4 {
		width: 75%;
	}
	.tab-full {
		width: 100%;
	}
}

/**
 * large mobile devices
 * ---------------------------------------------------------------
 */

@media screen and (max-width:600px) {
	.row {
		padding-left: 25px;
		padding-right: 25px;
	}
	.row .row {
		margin-left: -10px;
		margin-right: -10px;
	}
	[class*="col-"] {
		padding: 0 10px;
	}
	.mob-1-4 {
		width: 25%;
	}
	.mob-1-2 {
		width: 50%;
	}
	.mob-3-4 {
		width: 75%;
	}
	.mob-full {
		width: 100%;
	}
}

/**
 * small mobile devices
 * ---------------------------------------------------------------
 */

@media screen and (max-width:400px) {
	.row {
		padding-left: 30px;
		padding-right: 30px;
	}
	.row .row {
		padding-left: 0;
		padding-right: 0;
		margin-left: 0;
		margin-right: 0;
	}
	[class*="col-"] {
		width: 100% !important;
		float: none !important;
		clear: both !important;
		margin-left: 0;
		margin-right: 0;
		padding: 0;
	}
	[class*="col-"] + [class*="col-"]:last-child {
		float: none;
	}
}

.block-1-6 .bgrid {
	width: 16.66667%;
}
.block-1-4 .bgrid {
	width: 25%;
}
.block-1-3 .bgrid {
	width: 33.33333%;
}
.block-1-2 .bgrid {
	width: 50%;
}
.block-1-6 .bgrid:nth-child(6n+1),
.block-1-4 .bgrid:nth-child(4n+1),
.block-1-3 .bgrid:nth-child(3n+1),
.block-1-2 .bgrid:nth-child(2n+1) {
	clear: both;
}

@media screen and (max-width:1024px) {
	.block-s-1-6 .bgrid {
		width: 16.66667%;
	}
	.block-s-1-4 .bgrid {
		width: 25%;
	}
	.block-s-1-3 .bgrid {
		width: 33.33333%;
	}
	.block-s-1-2 .bgrid {
		width: 50%;
	}
	.block-s-full .bgrid {
		width: 100%;
		clear: both;
	}
	[class*="block-s-"] .bgrid:nth-child(n) {
		clear: none;
	}
	.block-s-1-6 .bgrid:nth-child(6n+1),
	.block-s-1-4 .bgrid:nth-child(4n+1),
	.block-s-1-3 .bgrid:nth-child(3n+1),
	.block-s-1-2 .bgrid:nth-child(2n+1) {
		clear: both;
	}
}

/**
 * tablets
 * ---------------------------------------------------------------
 */
@media screen and (max-width:768px) {
	.block-tab-1-6 .bgrid {
		width: 16.66667%;
	}
	.block-tab-1-4 .bgrid {
		width: 25%;
	}
	.block-tab-1-3 .bgrid {
		width: 33.33333%;
	}
	.block-tab-1-2 .bgrid {
		width: 50%;
	}
	.block-tab-full .bgrid {
		width: 100%;
		clear: both;
	}
	[class*="tab-bgrid-"] .bgrid:nth-child(n) {
		clear: none;
	}
	.block-tab-1-6 .bgrid:nth-child(6n+1),
	.block-tab-1-4 .bgrid:nth-child(4n+1),
	.block-tab-1-3 .bgrid:nth-child(3n+1),
	.block-tab-1-2 .bgrid:nth-child(2n+1) {
		clear: both;
	}
}

/**
 * mobile devices
 * ---------------------------------------------------------------
 */
@media screen and (max-width:600px) {
	.block-mob-1-6 .bgrid {
		width: 16.66667%;
	}
	.block-mob-1-4 .bgrid {
		width: 25%;
	}
	.block-mob-1-3 .bgrid {
		width: 33.33333%;
	}
	.block-mob-1-2 .bgrid {
		width: 50%;
	}
	.block-mob-full .bgrid {
		width: 100%;
		clear: both;
	}
	[class*="mob-bgrid-"] .bgrid:nth-child(n) {
		clear: none;
	}
	.block-mob-1-6 .bgrid:nth-child(6n+1),
	.block-mob-1-4 .bgrid:nth-child(4n+1),
	.block-mob-1-3 .bgrid:nth-child(3n+1),
	.block-mob-1-2 .bgrid:nth-child(2n+1) {
		clear: both;
	}
}

/**
 * stack on small mobile devices
 * ---------------------------------------------------------------
 */

@media screen and (max-width:400px) {
	.stack .bgrid {
		width: 100% !important;
		float: none !important;
		clear: both !important;
		margin-left: 0;
		margin-right: 0;
	}
}

/**
 * ===================================================================
 * MISC
 *
 * -------------------------------------------------------------------
 */

/**
 * Clearing - (http://nicolasgallagher.com/micro-clearfix-hack/
 * -
 */

.group:before, .group:after {
	content: "";
	display: table;
}
.group:after {
	clear: both;
}

/**
 * Misc Helper Styles
 * -
 */

.hide { display: none; }
.invisible { visibility: hidden; }
.antialiased {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.remove-bottom { margin-bottom: 0; }
.half-bottom { margin-bottom: 1.5rem !important; }
.add-bottom { margin-bottom: 3rem !important; }
.no-border { border: none; }
.full-width { width: 100%; }
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }
.pull-left { float: left; }
.pull-right { float: right; }
.align-center {
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}



